/* AM PM TOGGLE */
/*  NOTE: from: https://codepen.io/nickbottomley/pen/uhfmn  */
/* main styles */
.switch {
  margin-left: 175px;
  position: absolute;
  top: 8px;
}

.switch input {
  position: absolute;
  top: 0;
  z-index: 2;
  opacity: 0;
  cursor: pointer;
}

.switch input:checked {
  z-index: 1;
}

.switch input:checked + label {
  opacity: 1;
  cursor: default;
}

.switch input:not(:checked) + label:hover {
  opacity: 0.5;
}

.switch label {
  color: #fff;
  opacity: 0.33;
  -webkit-transition: opacity 0.25s ease;
  transition: opacity 0.25s ease;
  cursor: pointer;
}

.switch .toggle-outside {
  height: 100%;
  border-radius: 2rem;
  padding: 0.25rem;
  overflow: hidden;
  -webkit-transition: 0.25s ease all;
  transition: 0.25s ease all;
}

.switch .toggle-inside {
  border-radius: 5rem;
  background: #4a4a4a;
  position: absolute;
  -webkit-transition: 0.25s ease all;
  transition: 0.25s ease all;
}

.switch-horizontal input {
  height: 3rem;
  width: 6rem;
  left: 6rem;
  margin: 0;
}

.switch-horizontal label {
  font-size: 20px;
  line-height: 3rem;
  display: inline-block;
  width: 6rem;
  height: 100%;
  margin: 0;
  text-align: center;
}

.switch-horizontal label:last-of-type {
  margin-left: 60px;
}

.switch-horizontal .toggle-outside {
  background: #fff;
  height: 32px;
  position: absolute;
  width: 80px;
  left: 6rem;
}

.switch-horizontal .toggle-inside {
  height: 2rem;
  width: 2rem;
}

.switch-horizontal input:checked ~ .toggle-outside .toggle-inside {
  left: 0.25rem;
}

.switch-horizontal input ~ input:checked ~ .toggle-outside .toggle-inside {
  left: 3.25rem;
}

.overlay-wrapper {
  width: 100%;
}

.overlay {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: #ddd;
  z-index: -1;
  filter: blur(8px);
  -webkit-filter: blur(8px);
  -moz-filter: blur(8px);
  -o-filter: blur(8px);
  -ms-filter: blur(8px);
}

