  position: relative;
  -webkit-appearance: none;
  padding-right: 10px;
}

.ios-style-dropdown:focus {
  outline: none;
}

.plain-selector {
  margin: 0 3px;
}

.plain-selector::after {
  content: "";
  position: absolute;
  z-index: 2;
  bottom: 10%;
  margin-top: -3px;
  height: 0;
  width: 0;
  right: -5px;
  border-top: 6px solid black;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  pointer-events: none;
}

.plain-selector::before {
  content: "";
  position: absolute;
  z-index: 2;
  top: 20%;
  margin-top: -3px;
  height: 0;
  width: 0;
  right: -5px;
  border-bottom: 6px solid black;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  pointer-events: none;
}

.out-selector {
  position: relative;
}

.outer-selection {
  width: 100%;
}

.selection {
  display: block;
  margin: auto;
  border-radius: 50px;
  background: white;
  color: #333;
  font-size: 13px;
  max-width: 360px;
  text-align: center;
  width: 100%;
  position: relative;
}

input[type="checkbox"].ios8-switch {
  position: absolute;
  margin: 8px 0 0 16px;
}

input[type="checkbox"].ios8-switch + label {
  position: relative;
  padding: 5px 0 0 50px;
  line-height: 2.0em;
}

input[type="checkbox"].ios8-switch + label:before {
  content: "";
  position: absolute;
  display: block;
  left: 0;
  top: 0;
  width: 40px;
  /* x*5 */
  height: 24px;
  /* x*3 */
  border-radius: 16px;
  /* x*2 */
  background: #fff;
  border: 1px solid #d9d9d9;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

input[type="checkbox"].ios8-switch + label:after {
  content: "";
  position: absolute;
  display: block;
  left: 0px;
  top: 0px;
  width: 24px;
  /* x*3 */
  height: 24px;
  /* x*3 */
  border-radius: 16px;
  /* x*2 */
  background: #fff;
  border: 1px solid #d9d9d9;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

input[type="checkbox"].ios8-switch + label:hover:after {
  -webkit-box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
          box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
}

input[type="checkbox"].ios8-switch:checked + label:after {
  margin-left: 16px;
}

input[type="checkbox"].ios8-switch:checked + label:before {
  background: #55D069;
}

/* BOARDED STATUS */
.boarded-wrapper {
